<!-- SystemSetting -->
<template>
    <div class="MyStructure">
        <div class="btn">
            <el-button :disabled=!buttonAuth type="primary" @click="dialogFormVisible = true">添加</el-button>
        </div>
        <div class="table">
            <el-table :data="structuredData" style="width: 100%">

                <el-table-column label="创建时间" width="auto">
                    <template slot-scope="scope">
                        <i class="el-icon-time"></i>
                        <span style="margin-left: 5px">{{ scope.row.date }}</span>
                    </template>
                </el-table-column>

                <el-table-column label="编号" width="auto">
                    <template slot-scope="scope">
                        <span style="margin-left: 5px">{{ scope.row.id }}</span>
                    </template>
                </el-table-column>

                <el-table-column label="名称" width="auto">
                    <template slot-scope="scope">
                        <span style="margin-left: 5px">{{ scope.row.name }}</span>
                    </template>
                </el-table-column>

                <el-table-column label="所属" width="auto">
                    <template slot-scope="scope">
                        <span style="margin-left: 5px">{{ scope.row.location }}</span>
                    </template>
                </el-table-column>

                <el-table-column label="经度" width="auto">
                    <template slot-scope="scope">
                        <span style="margin-left: 5px">{{ scope.row.jingdu }}</span>
                    </template>
                </el-table-column>

                <el-table-column label="纬度" width="auto">
                    <template slot-scope="scope">
                        <span style="margin-left: 5px">{{ scope.row.weidu }}</span>
                    </template>
                </el-table-column>

                <el-table-column label="操作">
                    <template slot-scope="scope">
                        <el-button :disabled=!buttonAuth size="mini" @click="openEditForm(scope.$index, scope.row)">编辑</el-button>
                        <el-button :disabled=!buttonAuth size="mini" type="danger" @click="deleteStructure(scope.$index, scope.row)">删除</el-button>
                    </template>
                </el-table-column>

            </el-table>
        </div>

        <!-- 添加表单 -->
        <el-dialog title="结构物添加" :visible.sync="dialogFormVisible">
            <el-form :model="addForm" :rules="rules" ref="addForm">

                <el-form-item label="编号" prop="id" label-width='80px' required>
                    <el-input v-model="addForm.id"></el-input>
                </el-form-item>

                <el-form-item label="名称" prop="name" label-width='80px' required>
                    <el-input v-model="addForm.name"></el-input>
                </el-form-item>

                <el-form-item label="所属桥梁" prop="location" label-width='80px'>
                    <el-input v-model="addForm.location"></el-input>
                </el-form-item>

                <el-form-item label="经度" prop="jingdu" label-width='80px'>
                    <el-input v-model="addForm.jingdu"></el-input>
                </el-form-item>

                <el-form-item label="纬度" prop="weidu" label-width='80px'>
                    <el-input v-model="addForm.weidu"></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="addStructure('addForm')">确 定</el-button>
            </div>
        </el-dialog>

        <!-- 编辑表单 -->
        <el-dialog title="结构物编辑" :visible.sync="editDialogFormVisible">
            <el-form :model="editForm" :rules="rules" ref="editForm">

                <el-form-item label="编号" prop="id" label-width='80px' required>
                    <el-input v-model="editForm.id" readonly></el-input>
                </el-form-item>

                <el-form-item label="名称" prop="name" label-width='80px' required>
                    <el-input v-model="editForm.name"></el-input>
                </el-form-item>

                <el-form-item label="所属桥梁" prop="location" label-width='80px'>
                    <el-input v-model="editForm.location"></el-input>
                </el-form-item>

                <el-form-item label="经度" prop="jingdu" label-width='80px'>
                    <el-input v-model="editForm.jingdu"></el-input>
                </el-form-item>

                <el-form-item label="纬度" prop="weidu" label-width='80px'>
                    <el-input v-model="editForm.weidu"></el-input>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogFormVisible = false">取 消</el-button>
                <el-button type="primary" @click="editStructure('editForm')">确 定</el-button>
            </div>
        </el-dialog>

    </div>
</template>


<script>
export default {
    name: 'MyStructure',

    data() {
        return {
            structuredData: [],
            dialogFormVisible:false,
            editDialogFormVisible:false,
            buttonAuth: false,
            addForm: {
                date:'',
                id:'',
                name:'',
                location:'',
                jingdu:'',
                weidu:'',
            },
            editForm: {
                date:'',
                id:'',
                name:'',
                location:'',
                jingdu:'',
                weidu:'',
            },
            rules: {
                id: [
                    { require: true, message: '请输入编号', trigger: 'blur' },
                    { min:3, max:10, message: '长度在3到10个字符', trigger: 'blur' }
                ],
                name: [
                    { require: true, message: '请输入名称', trigger: 'blur'},
                    { min:3, max:10, message: '长度在3到10个字符', trigger: 'blur' }
                ]
            }
        }
    },

    methods:{

        async updateData() {
            await this.$store.dispatch('initStructure')
            this.structuredData = this.$store.state.StructureStore.StructureList
        },

        addStructure(formName){
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.addForm.date = new Date().toLocaleString()
                    this.$store.dispatch('addStructure',this.addForm)
                    this.updateData()
                    this.dialogFormVisible = false
                    this.$refs[formName].resetFields();
                    this.$message({
                        type: 'success',
                        message: '添加成功!'
                    });
                } else {
                    this.$message.error('请提交符合规范的信息!')
                }
            })
        },

        openEditForm(index, structureToEdit) {
            this.editDialogFormVisible = true
            this.editForm = JSON.parse(JSON.stringify(structureToEdit))
        },

        editStructure(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.$store.commit('editStructure', this.editForm)
                    this.updateData()
                    this.editDialogFormVisible = false
                    this.$refs[formName].resetFields();
                    this.$message({
                        type: 'success',
                        message: '编辑成功!'
                    });
                } else {
                    this.$message.error('请提交符合规范的信息!')
                }
            })
        },

        deleteStructure(index,structureToDelete) {
            this.$confirm('是否永久删除该数据?', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.$message({
                    type: 'success',
                    message: '删除成功!'
                });
                this.$store.dispatch("deleteStructure", structureToDelete)
                this.updateData()
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });          
            });
        }
    },

    created() {
        this.updateData()
        // 更新buttonAuth
        this.buttonAuth = (this.$store.state.UserStore.token === 'admin' || this.$store.state.UserStore.token === 'user') ? true : false
    },
}
</script>


